.transaction-history {
  width: 450px;
  padding: 0px;
  margin-bottom: 25px;
  text-align: center;
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
}

.transaction-thead {
  font-size: 15px;
  color: #fff;
  background-color: rgb(65, 204, 204);
}

.row:nth-child(even) {
  background-color: rgba(199, 223, 238, 0.65);
}
